import React from 'react';
import ReactDOM from 'react-dom';
import "./banner.css";
import Banner from "./Banner.js"


//数据
let cData=[];
for(let i=0;i<5;i++){

	cData.push({
		id:(i+1),
		title:"",
		pic:require("./images/"+(i+1)+".jpg")
	});
	
}



/*
	@ data 传输过去的数据，也就是一个数组
	@ interval表示多少秒轮播图滑动
	@ speed 运动的速度
	@ step 默认是显示第几张图
*/
ReactDOM.render(<Banner data={cData} interval={2000} speed={500} step={1} />, document.getElementById('root'));


