/*
实现react中的轮播图的插件
*/
import React from 'react';
// import ReactDOM from 'react-dom';
import propTypes from "prop-types";

class Banner extends React.Component{
	//初始化传入过来的数据
	static defaultProps={
		data:[],
		interval:1,
		step:1,
		speed:1
	}
	//传入过来的数据进行了类型的限制
	static propTypes={
		data:propTypes.array,
		interval:propTypes.number,
		step:propTypes.number,
		speed:propTypes.number
	}

	
	constructor(props){
		super(props);
		this.state={
			step0:this.props.step
		}
	}

	//数据的传输只操作一次，所以在只执行一次的生命周期函数中操作
	componentWillMount(){
		//首先需要把数据进行变化就是第一张和最后一张都要进行克隆
		// console.log("willMount:"+this.state.step0);
		let cloneData=[];
		let{data}=this.props;
		for(let i=0;i<data.length;i++){
			cloneData.push(data[i]);
		}
		cloneData.unshift(data[data.length-1]);
		cloneData.push(data[0]);
		this.cloneData=cloneData;
		this.wrapLength=`${this.cloneData.length*1000}px`;
		this.wrapLeft=-(this.state.step0*1000)+"px";
		this.wrapTransition=this.props.speed+"ms";
		this.timer=setInterval(this.autoMove,this.props.interval);
	}

	autoMove=()=>{
		this.setState({
			step0:this.state.step0+1
		});
	}

	componentWillUpdate(prevProps,nextProps){
	
		if(nextProps.step0>this.cloneData.length-1){
			this.wrapTransition="0ms";
			this.setState({
				step0:1
			});
		}
		if(nextProps.step0<0){

			this.wrapTransition="0ms";
			this.setState({
				step0:this.cloneData.length-2
			});
		}
		// console.log("nextProps:",nextProps.step0);
		this.wrapLeft=-(nextProps.step0*1000)+"px";


	}
	
	componentDidUpdate(){

		/*当为第一张并且过度效果失去的时候，说明是从后边跨过来第一张的情况*/
		if(this.state.step0===1 && this.wrapTransition==="0ms"){
			/*最后一张其实就是第一张，在瞬间变回后，则由第一张跨到第二张的过度效果。*/
			setTimeout(()=>{	
				this.wrapTransition=this.props.speed+"ms";
				this.setState({
					step0:2
				});
			},0);
		}

		//这个条件只有点击事件才能够实现，轮播图是不会实现这个条件的
		if(this.state.step0===this.cloneData.length-2 && this.wrapTransition==="0ms"){
			setTimeout(()=>{	
				this.wrapTransition=this.props.speed+"ms";
				this.setState({
					step0:this.state.step0=this.cloneData.length-3
				});
			},0);
		}
	}
	render(){
		return <section className="container"
		 onMouseEnter={this.autoTimerStop}
		 onMouseLeave={this.autoTimerStart}
		 onClick={this.hadnlerMove}
		 >
		<ul className="wrapp" style={{width:this.wrapLength,left:this.wrapLeft,transition:this.wrapTransition}}>
			{
				this.cloneData.map((item,index)=>{
					
					return <li key={index}><img src={item.pic} alt=""/></li>;
				})
			}
		</ul>
		<ul className="focus">
			{	//这里是焦点
				
				this.props.data.map((item,index)=>{
					let focusIndex=this.state.step0-1;
					if(this.state.step0===6){
						focusIndex=0;
					}
					if(this.state.step0===0){
						focusIndex=this.props.data.length-1;
					}

					return <li key={index} className={focusIndex===index?"active":""}></li>;
				})
			}
		</ul>
		<a href="javascript:;" className=" arrow prev"> 《 </a>
		<a href="javascript:;" className="arrow next"> 》 </a>
	</section>;
	}


	autoTimerStop=()=>{
		clearInterval(this.timer);
	}
	autoTimerStart=()=>{
		this.timer=setInterval(this.autoMove,this.props.interval);
	}

	prevMove=()=>{
		this.setState({
			step0:this.state.step0-1
		});
		console.log(this.state.step0);
	}
	nextMove=()=>{
		this.setState({
			step0:this.state.step0+1
		});
	}

	/**通过事件委托的方式实现左边的轮播和右边轮播 */
	hadnlerMove=(ev)=>{
		let tag=ev.target,
			tagName=tag.nodeName,
			tagClassName=tag.className;
		//判断点击的是a标签，并且className中包含了arrow这个样式才可以
		let isAClass=tagClassName.match(/(^ +|)arrow( +|$)/);


		if(tagName==="A" && isAClass){
			//限制过快的点击，点击后等运动完成后才能进行下一次的点击
			console.log("click:"+this.isRun);
			if(this.isRun)return;
			this.isRun=true;

			setTimeout(()=>{
				this.isRun=false;
				console.log("xxxx:"+this.isRun);
			},500);
			console.log("最后的isRun的值"+this.isRun);
			if(/(^ +|)prev( +|$)/.test(tagClassName)){
				this.prevMove();
				return;
			}
			this.nextMove();
			
		}	

	}

}
export default Banner;